* {
/* border: 1px solid #000; */
    position: relative;
    font-family: monospace;
    box-sizing: border-box;
}

body, html {
    margin: 0px;
    padding: 0px;
    background-color: #FFF2DC;
}

.hidden {
    display: none;
}

.all {
    height: 100vh;
    padding-top: 30px;
    width: 40%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, 0%);
    box-shadow: none;
}

.all .top-bar {
    bottom: 20px;
}

.all .top-bar .function-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.all .top-bar .game-result {
    text-align: center;
}

.all .top-bar .function-btn .btn{
    border: 1px solid #000;
    padding: 6px 12px;
    border-radius: 7px;
    text-align: center;
    margin: 5px;
    color: white;
    cursor: pointer;
    background-color: rgb(243, 86, 86);

}
.all .board-piece {
    z-index: 10;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    /* background-color: rgba(122, 187, 93, .all .function-btn {0.8); */
    /* border: 1px solid rgba(122, 187, 93, 0.8); */
    height: 500px;
    width: 550px;
    display: flex;
    flex-wrap: wrap;
    cursor: pointer;
}

.black {
    /* background-color: rgba(7, 6, 6, 0.6); */
    /* background: radial-gradient(circle at center,black,rgb(78, 77, 77)); */
    background:radial-gradient(circle at 35% 25%,
    #000 0,#333 20%,#444 40%,#444 90%,#555 95%,#AAA 100%);
    box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.6);
}

.white {
    background:radial-gradient(circle at 35% 25%,
    #fff 0,#EEE 20%,#EEE 40%,#EEE 90%,#FFF 95%,#FFF 100%);
    box-shadow: 0px 0px 1px 0px rgba(255, 255, 255, 0.6);
}
.all .board-piece .piece {      
    left: -1%;
    top: -2.5%;
    box-sizing: border-box;
    width: 5.263%;
    height: 5.263%;
    border-radius: 100%;
    /* border: 1px solid rgb(82, 77, 77); */
}

.all .board-piece .piece:hover {
    /* border: 1px solid #000; */
    box-shadow: 0px 0px 10px 1px rgba(143, 137, 137, 0.6);
    background-color: rgba(143, 137, 137, 0.6);
}
.all .board-background {
    position: absolute;
    left: 49.1%;
    top: 48.2%;
    transform: translate(-50%, -50%);
    z-index: 1;
    background-color: rgba(238, 187, 93);
    height: 475px;
    width: 520px;   
    display: flex;
    flex-wrap: wrap;
    border: 1px solid #000;
}
.all .board-background .piece {    
    box-sizing: border-box;
    width: 5.556%;
    height: 5.556%;
    border: 1px solid #000;
}
.all .board-background .piece::before {
    content: "";
    position: absolute;
    right: -50%;
    bottom: -50%;
    border-radius: 100%;
    box-sizing: border-box;
    width: 80%;
    height: 80%;
    /* border: 1px solid red;  */
} 

.all .board-background .piece::after {
    content: "";
    position: absolute;
    right: 50%;
    bottom: 50%;
    border-radius: 100%;
    box-sizing: border-box;
    width: 80%;
    height: 80%;
    /* border: 1px solid blue;  */
} 

